<template>
    <div class="ArticleAdmin">
        <b-form>
            <input type="hidden" id="article-id" v-model="article.id" />
            <b-form-group label="Nome:" label-for="article-name">
                <b-form-input id="article-name" type="text"
                    v-model="article.name" required
                    :readonly="mode === 'remove'"
                    placeholder="Informe o nome da artigo" />
            </b-form-group>
            <b-form-group label="Descrio:" label-for="article-description">
                <b-form-input id="article-description" type="text"
                    v-model="article.description" required
                    :readonly="mode === 'remove'"
                    placeholder="Informe a descrio" />
            </b-form-group>
            <template v-if="mode === 'save'">
                <b-form-group
                    label="Imagem (URL):" label-for="article-imageUrl">
                    <b-form-input id="article-imageUrl" type="text"
                        v-model="article.imageUrl" required
                        :readonly="mode === 'remove'"
                        placeholder="Informe a URL da imagem" />
                </b-form-group>
                <b-form-group
                    label="Categoria:" label-for="article-categoryId">
                    <b-form-select
                        id="article-categoryId"
                        :options="categories"
                        v-model="article.categoryId" />
                </b-form-group>
                <b-form-group
                    label="Autor:" label-for="article-userId">
                    <b-form-select
                        id="article-userId"
                        :options="users"
                        v-model="article.userId" />
                </b-form-group>
                <b-form-group
                    label="Contedo:" label-for="article-content">
                    <vue-editor v-model="article.content"
                        placeholder="Informe o contedo do artigo" />
                </b-form-group>
                <b-button variant="primary"
                    @click="save">Salvar</b-button>
            </template>
            <b-button variant="danger" v-if="mode === 'remove'"
                @click="remove">Excluir</b-button>
            <b-button class="ml-2" @click="reset">Cancelar</b-button>
        </b-form>
        <hr>
        <b-table hover striped :items="articles" :fields="fields">
            <template slot="actions" slot-scope="data">
                <b-button variant="warning" @click="loadArticle(data.item)" class="mr-2">
                    <i class="fa fa-pencil"></i>
                </b-button>
                <b-button variant="danger" @click="loadArticle(data.item, 'remove')">
                    <i class="fa fa-trash"></i>
                </b-button>
            </template>
        </b-table>
        <b-pagination size="md"
            v-model="page"
            :total-rows="count"
            :per-page="limit">
        </b-pagination>
    </div>
</template>

<script>
import { VueEditor } from 'vue2-editor'
import { baseApiUrl, showError } from '@/global'
import axios from 'axios'

export default {
    name: 'ArticleAdmin',
    components: { VueEditor },
    data: function() {
        return {
            mode: 'save',
            article: {},
            articles: [],
            categories: [],
            users: [],
            page: 1,
            limit: 0,
            count: 0,
            fields: [
                { key: 'id', label: 'Cdigo', sortable: true },
                { key: 'name', label: 'Nome', sortable: true },
                { key: 'description', label: 'Descrio', sortable: true },
                { key: 'actions', label: 'Aes' }
            ]
        }
    },
    mounted() {
        this.loadCategories()
        this.loadUsers()
        this.loadArticles()
    },
    watch: {
        page() {
            this.loadArticles()
        }
    },
    methods: {
        loadArticles() {
            const url = `${baseApiUrl}/articles?page=${this.page}`

            axios.get(url).then(res => {
                this.articles = res.data.data
                this.count = res.data.count
                this.limit = res.data.limit
            })
        },
        loadArticle(article, mode = 'save') {
            this.mode = mode
            const url = `${baseApiUrl}/articles/${article.id}`

            axios.get(url).then(res => this.article = res.data)
        },
        reset() {
            this.mode = 'save'
            this.article = {}
            this.loadArticles()
        },
        save() {
            const method = this.article.id ? 'put' : 'post'
            const id = this.article.id ? `/${this.article.id}` : ''

            axios[method](`${baseApiUrl}/articles${id}`, this.article)
                .then(() => {
                    this.$toasted.global.success()
                    this.reset()
                })
                .catch(showError)
        },
        remove() {
            const id = this.article.id

            axios.delete(`${baseApiUrl}/articles/${id}`)
                .then(() => {
                    this.$toasted.global.success()
                    this.reset()
                })
                .catch(showError)
        },
        loadCategories() {
            const url = `${baseApiUrl}/categories`

            axios.get(url).then(res => {
                this.categories = res.data.map(category => {
                    return { value: category.id, text: category.path }
                })
            })
        },
        loadUsers() {
            const url = `${baseApiUrl}/users`

            axios.get(url).then(res => {
                this.users = res.data.map(user => {
                    return { value: user.id, text: `${user.name} - ${user.email}` }
                })
            })
        }
    }

}
</script>
