<template>
    <div class="UserAdmin">
        <b-form>
            <input type="hidden" id="user-id" v-model="user.id" />
            <b-row>
                <b-col md="6" sm="12">
                    <b-form-group label="Nome:" label-for="user-name">
                        <b-form-input id="user-name" type="text"
                            v-model="user.name" required
                            :readonly="mode === 'remove'"
                            placeholder="Informe o nome do usurio" />
                    </b-form-group>
                </b-col>
                <b-col md="6" sm="12">
                    <b-form-group label="E-mail:" label-for="user-email">
                        <b-form-input id="user-email" type="email"
                            v-model="user.email" required
                            :readonly="mode === 'remove'"
                            placeholder="Informe o e-mail do usurio" />
                    </b-form-group>
                </b-col>
            </b-row>
            <b-row v-show="mode === 'save'">
                <b-col sm="12">
                    <b-form-checkbox
                        id="user-admin"
                        v-model="user.admin"
                        class="mt-3 mb-3">
                        Administrador?
                    </b-form-checkbox>
                </b-col>
                <b-col md="6" sm="12">
                    <b-form-group label="Senha:" label-for="user-password">
                        <b-form-input id="user-password" type="password"
                            v-model="user.password" required
                            placeholder="Informe a senha do usurio" />
                    </b-form-group>
                </b-col>
                <b-col md="6" sm="12">
                    <b-form-group label="Confirmao de senha:" label-for="user-confirm-password">
                        <b-form-input id="user-confirm-password" type="password"
                            v-model="user.confirmPassword" required
                            placeholder="Confirme a senha do usurio" />
                    </b-form-group>
                </b-col>
            </b-row>
            <b-row>
                <b-col sm="12">
                    <b-button variant="primary" v-if="mode === 'save'"
                        @click="save">Salvar</b-button>
                    <b-button variant="danger" v-if="mode === 'remove'"
                        @click="remove">Excluir</b-button>
                    <b-button class="ml-2" @click="reset">Cancelar</b-button>
                </b-col>
            </b-row>
        </b-form>
        <hr>
        <b-table hover striped :items="users" :fields="fields">
            <template slot="actions" slot-scope="data">
                <b-button variant="warning" @click="loadUser(data.item)" class="mr-2">
                    <i class="fa fa-pencil"></i>
                </b-button>
                <b-button variant="danger" @click="loadUser(data.item, 'remove')">
                    <i class="fa fa-trash"></i>
                </b-button>
            </template>
        </b-table>
    </div>
</template>

<script>
import { baseApiUrl, showError } from '@/global'
import axios from 'axios'

export default {
    name: 'UserAdmin',
    data: function() {
        return {
            mode: 'save',
            user: {},
            users: [],
            fields: [
                { key: 'id', label: 'Cdigo', sortable: true },
                { key: 'name', label: 'Nome', sortable: true },
                { key: 'email', label: 'E-mail', sortable: true },
                { key: 'admin', label: 'Administrador', sortable: true,
                    formatter: value => value ? 'Sim' : 'No' },
                { key: 'actions', label: 'Aes' }
            ]
        }
    },
    mounted() {
        this.loadUsers()
    },
    methods: {
        loadUsers() {
            const url = `${baseApiUrl}/users`

            axios.get(url).then(res => this.users = res.data)
        },
        loadUser(user, mode = 'save') {
            this.mode = mode
            this.user = { ...user }
        },
        reset() {
            this.mode = 'save'
            this.user = {}
            this.loadUsers()
        },
        save() {
            const method = this.user.id ? 'put' : 'post'
            const id = this.user.id ? `/${this.user.id}` : ''
            const url = `${baseApiUrl}/users${id}`

            axios[method](url, this.user)
                .then(() => {
                    this.$toasted.global.success()
                    this.reset()
                })
                .catch(showError)
        },
        remove() {
            const id = this.user.id
            const url = `${baseApiUrl}/users/${id}`

            axios.delete(url)
                .then(() => {
                    this.$toasted.global.success()
                    this.reset()
                })
                .catch(showError)
        }
    }

}
</script>
